import React, { LiHTMLAttributes, ReactNode } from 'react';
import classNames from 'classnames';

import { SvgCheckLg } from '../Icons';
import { Label } from '../Typography';

import styles from './Stepper.module.scss';

export interface ProcessStepProps extends LiHTMLAttributes<HTMLLIElement> {
    children: ReactNode;
    status: 'current' | 'upcoming' | 'complete';
}

export const ProcessStep = ({
    children,
    status,
    className,
    ...props
}: ProcessStepProps) => {
    return (
        <li
            className={classNames(
                styles.stepper__step,
                styles[`stepper__step--${status}`],
                className
            )}
            aria-current={status === 'current' ? true : false}
            {...props}
        >
            <div
                className={classNames(
                    styles['stepper__step-line'],
                    styles[`stepper__step--${status}-line`]
                )}
            />
            <div className={styles['stepper__step-content']}>
                <div
                    className={classNames(
                        styles['stepper__step-indicator'],
                        styles[`stepper__step--${status}-indicator`]
                    )}
                >
                    {status === 'complete' && (
                        <SvgCheckLg
                            aria-hidden="true"
                            className={styles['stepper__step-icon']}
                        />
                    )}
                </div>
                {children && (
                    <Label
                        className={classNames(
                            styles['stepper__step-label'],
                            styles[`stepper__step--${status}-label`]
                        )}
                        weight="600"
                    >
                        {children}
                    </Label>
                )}
            </div>
        </li>
    );
};
