import React, { type OlHTMLAttributes } from 'react';
import classNames from 'classnames';

import styles from './Stepper.module.scss';

export interface StepperProps extends OlHTMLAttributes<HTMLOListElement> {
    children: React.ReactNode;
    className?: string;
    currentStepTitle?: string;
    currentStepLabel?: string;
    nextStepTitle?: string;
    nextStepLabel?: string;
}

export const Stepper = ({
    children,
    className,
    currentStepTitle,
    currentStepLabel,
    nextStepTitle,
    nextStepLabel,
    ...props
}: StepperProps) => {
    const hasCurrentMessage = currentStepTitle && currentStepLabel;
    const hasNextMessage = nextStepTitle && nextStepLabel;

    return (
        <div className={styles.base}>
            <ol className={classNames(styles.stepper, className)} {...props}>
                {children}
            </ol>
            {(hasCurrentMessage || hasNextMessage) && (
                <div className={styles.stepper__label}>
                    {hasCurrentMessage && (
                        <div className={styles['stepper__label-current']}>
                            {currentStepTitle}:{' '}
                            <span
                                className={
                                    styles['stepper__label-current-text']
                                }
                            >
                                {currentStepLabel}
                            </span>
                        </div>
                    )}
                    {hasNextMessage && (
                        <div className={styles['stepper__label-next']}>
                            {nextStepTitle}: {nextStepLabel}
                        </div>
                    )}
                </div>
            )}
        </div>
    );
};
